<template>
  <div class="galeria_container">
    <div class="row-container">
      <Card
        v-for="(path, index) in galeria"
        v-bind:key="index"
        class="card_galeria"
        :onHover="true"
        :shadow="false"
      >
        <img
          :src="'images/galeria/'+path"
          loading="lazy"
          width="100%"
          class="galeria_imagen"
          @click="modalHandler(path)"
        />
      </Card>
    </div>
    <modal v-if="showModal" @close="showModal = false">
      <img slot="body" loading="lazy" :src="'images/galeria/'+pathModal" class="galeria_imagen_modal" />
    </modal>
  </div>
</template>

<script>
import Card from "@/components/card";
import modal from "@/components/modal";
export default {
  name: "galeria_view",
  components: {
    Card,
    modal
  },
  data() {
    return {
      galeria: {
        type: JSON
      },
      showModal: {
        type: Boolean,
        default: false
      },
      pathModal: {
        type: String,
        default: ""
      }
    };
  },
  methods: {
    modalHandler(path) {
      this.pathModal = path;
      this.showModal = true;
    },
    async leerGaleria() {
      await fetch(`galeria.json`)
        .then(resp => resp.json())
        .then(json => {
          this.galeria = json;
          this.$storage.set('Galeria', JSON.stringify(this.galeria))
        });
    }
  },
  beforeMount() {
    let cachedData = this.$storage.get('Galeria', null)

    if (!cachedData){
      this.leerGaleria();
    } else {
      this.galeria = JSON.parse(cachedData)
    }
    
    this.showModal = false;
  }
};
</script>

