<script>
import Card from "@/components/card";
import Carousel from "@/components/carousel";
import cakecarousel from "@/components/CakeCarousel";

export default {
  name: "home",
  components: {
    Card,
    Carousel,
    cakecarousel
  },
  data() {
    return {
      carousels: {
        type: Array
      }
    };
  },
  methods: {
    async leerCarousel() {
      this.carousels = [];
      await fetch(`carousels.json`)
        .then(resp => resp.json())
        .then(json => {
          for(var i in json)
            this.carousels.push(json [i]);
          this.$storage.set('Carousels', JSON.stringify(this.carousels))
        });
    },

    getLength(indexf) {
      return console.log(indexf);
    }
  },
  beforeMount() {
    let cachedData = this.$storage.get('Carousels', null)

    if (!cachedData){
      this.leerCarousel();
    } else {
      this.carousels = JSON.parse(cachedData)
    }
  }
};
</script>

