<template>
  <div class="tortas-container">

    <div class="center-content">
      <h2 class="titles">SABORES DE PONQU√â</h2>
      <ul class="lista-tortas-simple">
        <li v-for="(value, index) in tortas.ponques" v-bind:key="index">{{value}}</li>
      </ul>

      <h2 class="titles">OTROS ALTERNADOS</h2>
      <ul class="lista-tortas-simple">
        <li v-for="(value, index) in tortas.otros" v-bind:key="index">{{value}}</li>
      </ul>

      <h2 class="titles">RELLENOS</h2>
      <ul class="lista-tortas-simple">
        <li v-for="(value, index) in tortas.rellenos" v-bind:key="index">{{value}}</li>
      </ul>

      <h2 class="titles">RELLENOS ESPECIALES CON TEXTURAS</h2>
      <ul class="lista-tortas-simple">
        <li v-for="(value, index) in tortas.especiales" v-bind:key="index">{{value}}</li>
      </ul>

    </div>

  </div>
</template>

<script>
export default {
  name: "tortas_view",
  data() {
    return {
      tortas: {
        type: JSON
      }
    };
  },
  methods: {
    async leerDulces() {
      await fetch(`tortas.json`)
        .then(resp => resp.json())
        .then(json => {
          this.tortas = json;
          this.$storage.set('Tortas', JSON.stringify(this.tortas))
        });
    },
  },
  beforeMount() {
    let cachedData = this.$storage.get('Tortas', null)

    if (!cachedData){
      this.leerDulces();
    } else {
      this.tortas = JSON.parse(cachedData)
    }
  }
};
</script>

