import Vue from "vue";
import VueRouter from "vue-router";
import home_view from "../views/home_view.vue";
import contact_view from "../views/contact_view.vue";
import galeria_view from "../views/galeria_view.vue";
import dulces_view from "../views/dulces_view.vue";
import salados_view from "../views/salados_view.vue";
import tortas_view from "../views/sabores_view.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home_view",
    component: home_view
  },
  {
    path: "/contacto",
    name: "contact_view",
    component: contact_view
  },
  {
    path: "/galeria",
    name: "galeria_view",
    component: galeria_view
  },
  {
    path: "/dulces",
    name: "dulces_view",
    component: dulces_view
  },
  {
    path: "/salados",
    name: "salados_view",
    component: salados_view
  },
  {
    path: "/sabores",
    name: "tortas_view",
    component: tortas_view
  }
];

const router = new VueRouter({
  mode: 'history',
  routes
});

export default router;
